import React from 'react';
import { Form, Input, Button, Select, Radio} from 'antd';
import { Link } from 'react-router-dom';
import '../register/register.css';
const { Option } = Select;

const RegisterPage = () => {
    const onFinish = (values) => {
        console.log('Received values:', values);
        // X l logic ng k  y
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    return (
        <div style={{ maxWidth: '1000px', margin: 'auto' }}>
            <h2>ng k ti khon</h2>
            <Form
                name="register"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
                initialValues={{ remember: true }}
                layout="horizontal"
            >
                <Form.Item
                    label="H v tn"
                    name="fullName"
                    rules={[{ required: true, message: 'Vui lng nhp h v tn!' }]}
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 16 }}
                >
                    <Input style={{ width: '100%', maxWidth: '600px' }} />
                </Form.Item>

                <Form.Item
                    label="Ngy sinh"
                    name="dob"
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 16 }}
                    rules={[{ required: true, message: 'Vui lng chn ngy sinh!' }]}
                >
                    <div style={{ display: 'flex' }}>
                        {/* Combobox cho ngy */}
                        <Form.Item
                            name={['dob', 'day']}
                            noStyle
                            rules={[{ required: true, message: 'Vui lng chn ngy!' }]}
                        >
                            <Select defaultValue="1" style={{ width: 150, marginRight: 10 }}>
                                {[...Array(31)].map((_, index) => (
                                    <Option key={index + 1} value={`${index + 1}`}>
                                        {index + 1}
                                    </Option>
                                ))}
                            </Select>
                        </Form.Item>

                        {/* Combobox cho thng */}
                        <Form.Item
                            name={['dob', 'month']}
                            noStyle
                            rules={[{ required: true, message: 'Vui lng chn thng!' }]}
                        >
                            <Select defaultValue="1" style={{ width: 200, marginRight: 10 }}>
                                {[...Array(12)].map((_, index) => (
                                    <Option key={index + 1} value={`${index + 1}`}>
                                        Thng {index + 1}
                                    </Option>
                                ))}
                            </Select>
                        </Form.Item>

                        {/* Combobox cho nm */}
                        <Form.Item
                            name={['dob', 'year']}
                            noStyle
                            rules={[{ required: true, message: 'Vui lng chn nm!' }]}
                        >
                            <Select defaultValue="2022" style={{ width: 230 }}>
                                {[...Array(100)].map((_, index) => (
                                    <Option key={index + 1970} value={`${index + 1970}`}>
                                        {index + 1970}
                                    </Option>
                                ))}
                            </Select>
                        </Form.Item>
                    </div>
                </Form.Item>

                <Form.Item
                    label="Gii tnh"
                    name="gender"
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 16 }}
                    rules={[{ required: true, message: 'Vui lng chn gii tnh!' }]}
                >
                    <Radio.Group className="custom-radio">
                        <Radio value="male">Nam</Radio>
                        <Radio value="female">N</Radio>
                    </Radio.Group>
                </Form.Item>

                <Form.Item
                    label="CCCD"
                    name="cccd"
                    rules={[{ required: true, message: 'Vui lng nhp CCCD!' }]}
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 16 }}
                >
                    <Input style={{ width: '100%', maxWidth: '600px' }} />
                </Form.Item>

                <Form.Item
                    label="S in thoi"
                    name="phone"
                    rules={[{ required: true, message: 'Vui lng nhp s in thoi!' }]}
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 16 }}
                >
                    <Input style={{ width: '100%', maxWidth: '600px' }} />
                </Form.Item>

                <Form.Item
                    label="a ch"
                    name="address"
                    rules={[{ required: true, message: 'Vui lng nhp a ch!' }]}
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 16 }}
                >
                    <Input style={{ width: '100%', maxWidth: '600px' }} />
                </Form.Item>

                <Form.Item
                    label="Email"
                    name="email"
                    rules={[{ required: true, message: 'Vui lng nhp email!' }]}
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 16 }}
                >
                    <Input style={{ width: '100%', maxWidth: '600px' }} />
                </Form.Item>

                <Form.Item
                    label="Mt khu"
                    name="password"
                    rules={[{ required: true, message: 'Vui lng nhp mt khu!' }]}
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 16 }}
                >
                    <Input.Password style={{ width: '100%', maxWidth: '600px', marginLeft: 'auto' }} />
                </Form.Item>

                <Form.Item
                    label="Xc nhn mt khu"
                    name="confirmPassword"
                    dependencies={['password']}
                    rules={[
                        { required: true, message: 'Vui lng xc nhn mt khu!' },
                        ({ getFieldValue }) => ({
                            validator(_, value) {
                                if (!value || getFieldValue('password') === value) {
                                    return Promise.resolve();
                                }
                                return Promise.reject(new Error('Mt khu xc nhn khng khp!'));
                            },
                        }),
                    ]}
                    labelCol={{ span: 8 }}
                    wrapperCol={{ span: 16 }}
                >
                    <Input.Password style={{ width: '100%', maxWidth: '600px', marginLeft: 'auto' }} />
                </Form.Item>

                <div style={{ textAlign: 'center' }}>
                    <Link to="/dang-nhap"> c ti khon? ng nhp ngay!</Link>
                </div>

                <Form.Item style={{ textAlign: 'center' }}>
                    <Button type="primary" htmlType="submit" style={{ alignItems:'center',width: '30%', backgroundColor: '#111467', marginTop: '20px' }}>
                        ng k
                    </Button>
                </Form.Item>
            </Form>
        </div>
    );
};

export default RegisterPage;
